.portfolio{
    background-color: black;
    padding: 15% 1%;
    &__descr {
        color: #f1f1f1;
        text-align: center;
        font-size: 1.8vw;
    }
    &__img_wrapper {
        padding: 5% 0;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
    }
    &__img {
        width: 24%;
        height: auto;
        background: burlywood;
        margin-bottom: 15px;
        &:hover {
            transform: scale(1.3);
            cursor: pointer;
        }
    }
}